'use client'

import Hero from "@/components/Hero";
import CompanyOverview from "@/components/CompanyOverview";
import CascadingText from "@/components/CascadingText";

export default function HomePage() {
  const scrollToMission = () => {
    const missionSection = document.getElementById('mission-section');
    if (missionSection) {
      missionSection.scrollIntoView({ behavior: 'smooth', block: 'start' });
    }
  };

  return (
    <div>
      <Hero onLearnMore={scrollToMission} onWatchDemo={() => {}} />
      
      {/* Mission Section */}
      <section id="mission-section" className="flex items-center justify-center px-4 sm:px-6 lg:px-8 py-16">
        <div className="max-w-5xl mx-auto text-center space-y-12">
          <CascadingText 
            text="Our Mission" 
            font="inter"
            fontSize="responsive"
          />
          <p className="text-xl md:text-2xl pt-[-24] lg:text-3xl text-muted-foreground leading-relaxed font-light max-w-4xl mx-auto">
            To revolutionize technology through innovative solutions that empower businesses and individuals to achieve extraordinary outcomes while maintaining the highest standards of quality and reliability.
          </p>
        </div>
      </section>

      <CompanyOverview onExploreMore={() => {}} />
    </div>
  );
}